.available_snacks_container {
  max-width: 60rem;
  width: 90%;
  margin: 2rem auto;
  animation: snacks-appear 1s ease-out forwards;

  .available_snacks_list {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}

@keyframes snacks-appear {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
